@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100;400;700&display=swap");
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@500;600;700&display=swap');

/* base */
html,body,div,span,h1,h2,h3,h4,h5,h6,p,blockquote,pre,abbr,address,cite,code,del,dfn,em,img,ins,kbd,q,samp,small,strong,sub,sup,b,i,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,
tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,figcaption,figure,footer,header,menu,nav,section,main,summary,time,mark,audio,video 
{margin: 0;padding: 0;border: 0;outline: 0;box-sizing: border-box;background: transparent;}
html,body {width: 100%;overflow-x: hidden;background:#f4f3f7;}
body {font-size: 14px;font-family: "Montserrat", sans-serif;letter-spacing: 0;overflow: hidden;line-height: 1.41;letter-spacing: -0.02em;color: #fff;font-weight: 300;}
ul,ol {list-style-type: none;}
a {box-sizing: border-box;color: #fff;text-decoration: none;cursor: pointer;}
a:hover,a:active {text-decoration: none;}
button {margin: 0;padding: 0;box-sizing: border-box;border: 0 none;background-color: transparent;vertical-align: middle;color: #333;font-family: sans-serif;cursor: pointer;outline: none;}
figcaption {display: none;}
img {width: 100%;vertical-align: top;}
input, textarea {font-family: "Montserrat", sans-serif;}
input:focus, textarea:focus {border:none}

.main {max-width:640px;margin:0 auto;padding:50px 0 70px;text-align: center;background:#f4f3f7;}
.main h1 {font-family:Montserrat,sans-serif;color:#b04eab;font-size: 36px;;}
.main h1 span {display: block;margin: 10px auto 0;width: 100px;font-size: 13px;padding: 3px 10px;border: 1px solid purple;border-radius: 100px;font-weight: 400;;}
.main .progress {position: relative;width: 200px;height: 200px;margin: 35px auto 0;box-shadow: 0px 9px 12px #d8d8d8;border-radius: 100%;}
.main .progress:before {content: "";position: absolute;width: 165px;height: 165px;background: #fff;border-radius: 100%;top: 50%;left: 50%;transform: translate(-50%, -50%);box-shadow:0 0px 11px #e7e7e7;z-index: 99;}
.main .progress .precent {font-family:Montserrat,sans-serif;position: absolute;top: 50%;left: 50%;transform: translate(-50%, -50%); width:100%; font-size: 35px;color: #e995e5;z-index: 99;}
.main .progress .circle {width: 200px;height: 200px;background-image: linear-gradient(135deg, #e318ea 10%, #6407d0 100%);-webkit-clip-path: polygon(0% 100%,0% 0%,100% 0%,50% 50%,100% 0%,100% 100%);clip-path: polygon(0% 100%, 0% 0%, 100% 0%, 50% 50%, 100% 0%, 100% 100%);animation-name: loading;animation-duration: 5s;animation-iteration-count: infinite;
animation-timing-function: cubic-bezier(0, 0.1, 0.9, 0.81);animation-play-state: paused;animation-direction: reverse;animation-delay: 5s;border-radius: 100%;}

.view {width:100%;max-width:640px;height:calc(100vh - 422px);margin:-20px auto 0;padding:20px 15px;background-color: #f9f9f9;border-top-left-radius: 20px;border-top-right-radius: 20px;overflow: auto;}
.view .add {position:relative;}
.view .add input {width:100%;padding:12px 15px;color:#8978ac;background:#e6e2ed;border:none;box-sizing: border-box;border-radius: 20px;}
.view .add .btn_add{position:absolute;top:50%;right:0;transform:translate(-5px,-50%);width:30px;height:30px;background:#69568f;font-size: 0;border-radius: 30px;;}
.view .add .btn_add:before, .view .add .btn_add:after{content: "";position: absolute;background-color: #fff;}
.view .add .btn_add:before{top: 50%;left: 50%; transform: translate(-50%,-50%); width: 1px;height: 50%; }
.view .add .btn_add:after{ top: 50%;left: 50%; transform: translate(-50%,-50%);width: 50%;height: 1px; }
.view .list b {display:block;margin:30px 0 12px;color:#9f91bc;}
.view .list .item {position:relative;margin-bottom: 13px;padding:10px;box-shadow :0px 1px 6px #c8bfdb; border-radius: 10px;;background:#f4f3f7;}
.view .list .item label {position:relative;display:block;width:calc(100% - 50px);min-height:25px;padding-left:36px;font-size:0;overflow:hidden;}
.view .list .item label input {position:absolute;top:0;left:-19px;width:0;height:0;opacity:0;}
.view .list .item label span {display:inline-block;margin-top:6px;color:#8978ac;font-size:13px;font-weight:400;line-height:1;}
.view .list .item label span:before {content:'';position:absolute;top:47%;left:5px;transform:translate(0,-50%);width:16px;height:16px;box-shadow :0px 1px 6px #c8bfdb;border-radius:100%;display:block;box-sizing:border-box;}
.view .list .item label.finish span {text-decoration: line-through;opacity: 0.7;}
.view .list .item label.finish span:before {background-color:#8978ac;border:0;}
.view .list .item .btn_remove {position: absolute;top: 8px;right: 7px;display: block;padding: 6px 7px;box-shadow: 0px 1px 6px #c8bfdb;border-radius: 8px;box-sizing: border-box;
color: #c0b3da;font-size: 12px;}

@keyframes loading {
  0% {-webkit-clip-path: polygon(0% 100%,0% 0%,100% 0%,50% 50%,100% 0%,100% 100%);clip-path: polygon(0% 100%, 0% 0%, 100% 0%, 50% 50%, 100% 0%, 100% 100%);}
  25% {-webkit-clip-path: polygon(0% 100%,0% 0%,100% 0%,50% 50%,100% 100%,100% 100%);clip-path: polygon(0% 100%, 0% 0%, 100% 0%, 50% 50%, 100% 100%, 100% 100%);}
  50% {-webkit-clip-path: polygon(0% 100%,0% 0%,100% 0%,50% 50%,0% 100%,0% 100%);clip-path: polygon(0% 100%, 0% 0%, 100% 0%, 50% 50%, 0% 100%, 0% 100%);}
  75% {-webkit-clip-path: polygon(0% 100%, 0% 0%, 100% 0%, 50% 50%, 0% 0%, 0% 0%);clip-path: polygon(0% 100%, 0% 0%, 100% 0%, 50% 50%, 0% 0%, 0% 0%);}
  100% {-webkit-clip-path: polygon(0% 100%,0% 0%,100% 0%,50% 50%,100% 0%,0% 0%);clip-path: polygon(0% 100%, 0% 0%, 100% 0%, 50% 50%, 100% 0%, 0% 0%);}
}